import * as Yup from "yup";

const emailRegex = "^[a-z0-9](.?[a-z0-9]){5,}@g(oogle)?mail.com$";

const validationSchemaForm = Yup.object().shape({
  name: Yup.string().required(" name !"),
  age: Yup.number()
    .min(18, "    18")
    .required(" age !"),
  email: Yup.string()
    .email()
    .matches(
      emailRegex,
      "       ...@gmail.com"
    )
    .required(" email !"),
  password: Yup.string()
    .min(6, "Password    6")
    .max(26, "Password    26")
    .required(" password !"),
});

export { validationSchemaForm };
